<template>
    <div class="mine-navbar">
        <!--    -->
        <div class="mine-navbar-left">
            <slot name="left"></slot> 
            <!--   -->
        </div>
        <div class="mine-navbar-center">
            <slot name="center"></slot>
        </div>
        <div class="mine-navbar-right">
            <slot name="right"></slot>
        </div>
        <!--  -->
        <h1 class="mine-navbar-title" v-if="title">
            <!-- flex  -->
            <span v-text="title" class="mine-navbar-text"></span>
        </h1>
    </div>
</template>

<script>
    export default{
        name:'MeNavBar',
        data(){
            return{

            }
        },
        props:{
            // title
            title:{
                type:String,
                default:''
            }
        }
    }
</script>
<style lang="scss" scoped>
    @import "~@/assets/scss/mixins";

    .mine-navbar{
        @include flex-between();
        height: 50px;
        background-color: #fff;

        .mine-navbar-left{
            margin-left: 10px;
        }
        .mine-navbar-center{
            margin: 0 10px 0;
            flex:1;
        }
        .mine-navbar-right{
            margin-right: 10px;
        }
        .mine-navbar-title{
            position: absolute;
            top: 0;
            bottom: 0;
            left: 20%;
            right: 20%;
            @include flex-center();
            text-align: center;
        }   
        .mine-navbar-text{
            font-size: 18px;
            @include ellipsis();
            width: 100%;
            line-height: 1.5; 
        }
    }
    
</style>


