<!-- scroll  -->
<!--   -->
<!--  absolute   -->
<!--  -->
<!-- wrapper  -->
<!--  -->

<template>
    <swiper :options="swiperOption" ref="swiper">
        <div class="mine-scroll-pull-down" v-if="pullDown">
            <me-loading :text="pullDownText" inline ref="pullDownLoading"></me-loading>
        </div>
        <swiper-slide>
            <slot></slot>
        </swiper-slide>
        <div class="mine-scroll-pull-up" v-if="pullUp">
            <me-loading :text="pullUpText" inline ref="pullUpLoading"></me-loading>
        </div>
        <div class="swiper-scrollbar" v-if="scrollbar" slot="scrollbar"></div>
    </swiper>
</template>

<script>
    import {swiper,swiperSlide} from 'vue-awesome-swiper';
    import MeLoading from '@/base/loading';
    // 
    import {PULL_DOWN_HEIGHT,PULL_DOWN_TEXT_INIT,PULL_DOWN_TEXT_START,PULL_DOWN_TEXT_ING,PULL_DOWN_TEXT_END,PULL_UP_HEIGHT,PULL_UP_TEXT_INIT,PULL_UP_TEXT_START,PULL_UP_TEXT_ING,PULL_UP_TEXT_END} from './config'

    export default{
        name:"MeScroll",
        data(){
            return{
                
            }
        },
        components:{
            swiper,swiperSlide,
            'me-loading':MeLoading
        },
        props:{
            scrollbar:{
                type:Boolean,
                default:true
            },
            data:{
                type:[Array,Object]
            },
            pullDown:{
                // 
                type:Boolean,
                default:false
            },
            pullUp:{
                // 
                type:Boolean,
                default:false
            },
            // pullDownText:{
                // type:String,
                // default:''
            // }
        },
        watch:{
            //watch props  
            //data    datapropsdatadata
            data(){
                this.update();
            }
        },
        created(){
            this.init();
        },
        methods:{
            init(){
                this.pulling = false;  //
                this.pullDownText = PULL_DOWN_TEXT_INIT;
                this.pullUpText = PULL_UP_TEXT_INIT;
                this.swiperOption = {
                    direction: 'vertical', //  
                    slidesPerView: 'auto', // slider slides
                    freeMode: true,  //  
                    setWrapperSize:true, //  swiperwrapper
                    scrollbar:{
                        el:this.scrollbar ? '.swiper-scrollbar' : null,
                        hide:true  //
                    },
                    on:{
                        //  sliderMoveslider
                        //touchEnd 
                        //transitionEnd 
                        sliderMove:this.scroll,
                        touchEnd:this.touchEnd,
                        transitionEnd:this.scrollEnd
                    }
                };
            },

            scrollToTop(speed,runCallbacks){
                //api  
                //slideTo 
                this.$refs.swiper && this.$refs.swiper.swiper.slideTo(0,speed,runCallbacks);
            },

            scrollEnd(){
                //scroll-end   
                // pulling
                this.$emit('scroll-end',this.$refs.swiper.swiper.translate,this.$refs.swiper.swiper,this.pulling);
            },

            update(){
                //swiperupdate() swiper
                //this.$refsrefhtmlclass 
                this.$nextTick(()=>{
                    // dom
                    this.$refs.swiper && this.$refs.swiper.swiper.update();
                })
            },

            scroll(){
                //
                const swiper = this.$refs.swiper.swiper;

                //  scroll 
                //
                this.$emit('scroll',swiper.translate,this.$refs.swiper.swiper);

                ///
                if(this.pulling){return;}

                if(swiper.translate > 0){
                    //  
                    if(!this.pullDown) return;
                    //pullDownfalse return

                    if(swiper.translate > PULL_DOWN_HEIGHT){
                        // 
                        //this.pullDownText = ** 
                        //bug 
                        this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_START);
                    }else{
                        //
                        this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_INIT);
                    }
                }else if(swiper.isEnd){
                    //    swiper.isEnd
                    if(!this.pullUp) return;
                    //pullDownfalse return
                    //

                    //swiper.$warpperEl css
                    //swiperwrapper
                    // + -  >  
                    //abs
                    const totalHeight = parseInt(swiper.$wrapperEl.css('height'));
                    const isPullUp = Math.abs(swiper.translate) + swiper.height - PULL_UP_HEIGHT > totalHeight;
                    if(isPullUp){
                        // 
                        //this.pullDownText = ** 
                        //bug 
                        this.$refs.pullUpLoading.setText(PULL_UP_TEXT_START);
                    }else{
                        //
                        this.$refs.pullUpLoading.setText(PULL_UP_TEXT_INIT);
                    }
                }
            },
            touchEnd(){
                //  
                const swiper = this.$refs.swiper.swiper;
                //

                 ///
                if(this.pulling){return;}

                if(swiper.translate > PULL_DOWN_HEIGHT){
                    //   
                    if(!this.pullDown) return;
                    //pullDownfalse return
                    
                    this.pulling = true;

                    swiper.allowTouchMove = false; //
                    // 
                    swiper.setTransition(swiper.params.speed);
                    swiper.setTranslate(PULL_DOWN_HEIGHT);
                    swiper.params.virtualTranslate = true; //
                    // 
                    this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_ING);
                    // 
                    // 
                    this.$emit('pull-down',this.pullDownEnd);
                }else if(swiper.isEnd){
                    //    swiper.isEnd
                    if(!this.pullUp) return;
                    //pullDownfalse return
                    //

                    //swiper.$warpperEl css
                    //swiperwrapper dom
                    // + -  >  
                    //abs
                    const totalHeight = parseInt(swiper.$wrapperEl.css('height'));
                    const isPullUp = Math.abs(swiper.translate) + swiper.height - PULL_UP_HEIGHT > totalHeight;
                    if(!this.pullDown) return;
                    //pullDownfalse return
                    
                    this.pulling = true;

                    swiper.allowTouchMove = false; //
                    // 
                    swiper.setTransition(swiper.params.speed);
                    swiper.setTranslate(-(totalHeight + PULL_UP_HEIGHT - swiper.height));
                    swiper.params.virtualTranslate = true; //
                    // 
                    this.$refs.pullUpLoading.setText(PULL_UP_TEXT_ING);
                    // 
                    // 
                    this.$emit('pull-up',this.pullUpEnd);
                }


            },
            pullDownEnd(){
                //
                const swiper = this.$refs.swiper.swiper;
                //false
                this.pulling = false;
                // 
                this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_END);
                swiper.allowTouchMove = true; //
                swiper.params.virtualTranslate = false; //
                // 
                swiper.setTransition(swiper.params.speed);
                swiper.setTranslate(0);
                setTimeout(()=>{
                    //  
                    this.$emit('pull-down-transition-end');
                },swiper.params.speed);
            },
            pullUpEnd(){
                //
                const swiper = this.$refs.swiper.swiper;
                //false
                this.pulling = false;
                // 
                this.$refs.pullUpLoading.setText(PULL_UP_TEXT_END);
                swiper.allowTouchMove = true; //
                swiper.params.virtualTranslate = false; //
            }
        }
    }
</script>

<style scoped lang="scss">
    //container
    .swiper-container{
        overflow: hidden;
        width: 100%;
        height: 100%;
    }
    //  
    .swiper-slide{
        height: auto;
    }

    //
    .mine-scroll-pull-down{
        position: absolute;
        left: 0;
        bottom: 100%;
        width:100%;
        height: 80px;
    }
    //
    .mine-scroll-pull-up{
        position: absolute;
        left: 0;
        top: 100%;
        width:100%;
        height: 30px;
    }
</style>



