<!-- search-box -->
<template>
  <div class="mine-search-box-wrapper">
    <i class="iconfont icon-search"></i>
    <!-- div input -->
    <div class="mine-search-box" v-if="fake">{{placeholder}}</div>      
    <input
      class="mine-search-box"
      type="text"
      title=""
      :placeholder="placeholder"
      ref='input'
      v-if="!fake"
      v-model="query"           
    >
    <!-- v-model input  v-model v-modelinput -->
    <i
      class="iconfont icon-close" @click="reset" v-show="query"      
    ></i>
  </div>
</template>

<script> 
  import {debounce} from '@/assets/js/util';  // 

  export default {
    name: 'MeSearchBox',
    data(){
      return{
        query:''
      }
    },
    props:{
      placeholder:{
        type:String,
        default:''
      },
      fake:{
        type:Boolean,
        default:false
      }
    },
    watch:{
      query:debounce(function (){
        //timer  
        // 
        this.$emit('query',this.query);
      })
    },
    methods:{
      focus(){
        // 
        this.$refs.input &&  this.$refs.input.focus();
      },
      clear(){
        //
        this.query = '';
      },
      reset(){
        this.clear();
        this.focus();
      }
    }
  };
</script>

<style lang="scss" scoped>
  @import "~@/assets/scss/mixins";

  $search-box-height: 30px;

  .mine-search-box-wrapper {
    display: flex;
    align-items: center;
    width: 100%;
    height: $search-box-height;
    padding: 0 7px;
    background-color: #fff;
    border-radius: $search-box-height / 2;
  }

  .iconfont {
    color: $icon-color;
    font-size: $icon-font-size-sm;
    font-weight: bold;
  }

  .mine-search-box {
    flex: 1;
    background: none;
    border: none;
    margin: 0 6px;
    color: #666;
    line-height: 1.5;
  }
</style>



