<!-- loading  -->
<template>
    <!-- inline.mine-loading-inlineclass -->
    <div class="mine-loading" :class="{'mine-loading-inline': inline }">
        <!--  -->
        <span class="mine-loading-indicator" v-if="indicator === 'on'">
            <slot><img src="./loading.gif" alt="loading"></slot>
        </span>
        <span class="mine-loading-text" v-if="loadingText">{{loadingText}}</span>
    </div>
</template>

<script>
    
    export default{
        name:"MeLoading",
        data(){
            return{
                loadingText : this.text
            }
        },
        props:{
            indicator:{   //
                type:String,
                default:'on',
                validator(value){
                    //
                    return ['on','off'].indexOf(value) > -1;
                }
            },
            text:{       //
                type:String,
                default:'...'
            },
            inline:{    //
                type:Boolean,
                default:false
            }
        },
        methods:{
            setText(newText){
                // props
                // dataprops
                // 
                this.loadingText = newText;
            }
        },
        watch:{
            text(newText){
                //text propstext
                //
                this.loadingText = newText;
            }
        }   
    }
</script>

<style lang="scss" scoped>
    @import '~@/assets/scss/mixins';

    .mine-loading{
        overflow:hidden;
        width: 100%;
        height: 100%;
        @include flex-center(column);
        
        &.mine-loading-inline{
            //  flexrow
            flex-direction: row;
            .mine-loading-indicator ~ .mine-loading-text{
                // ~
                margin-top:0;
                margin-left: 6px;
            }
        }
    }

    .mine-loading-indicator ~ .mine-loading-text{
        // ~
        margin-top:6px;
    }

</style>


